import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import emoji from '@/components/emoji.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'emoji',
      component: emoji
    }
  ]
})



